import { Button, makeStyles } from '@material-ui/core';
import './index.css';

function App() {

  const useStyles = makeStyles(() => ({
    btn : {
      margin : "10px"
    },

  }));

  const classes = useStyles();

  const onBtnClick = (id)=>{
    window.UnityInAppBrowser.sendMessageFromJS(id);
  };


  return (
    <div className="App">
        <Button className={classes.btn} onClick={()=>onBtnClick("AK47Skin0")} variant="contained" color="primary">
          AK-47_1
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick("AK47Skin1")} variant="contained" color="primary">
          AK-47_2
        </Button>
        <Button className={classes.btn} onClick={()=>onBtnClick("AK47Skin2")} variant="contained" color="primary">
          AK-47_3
        </Button>
    </div>
  );
}

export default App;
